export default {
	data() {
		return {
			not: '',
			show1:false,
		}
	},
	onLoad() {
		this.notstart()
	},
	methods: {
		show(e) {
			console.log(e);
			uni.navigateTo({
				url: '/pages/show/show?data=' + encodeURIComponent(JSON.stringify(e))
			});
		},
		//未开始的会议
		notstart() {
			let _this = this;
			uni.request({
				url: _this.$loginUrl + '/api/v1/notstart', //仅为示例，并非真实接口地址。
				method: 'GET',
				success: (res) => {
					console.log(res.data);
					if (res.data.code == 200) {
						_this.not = res.data.data;
						_this.show1 = true;
					}
				},
				fail: (res) => {
					console.log(res.data);
				}
			});
		}
	}
}
